/** GRID ***/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
  max-width: 130rem;
}
@media (min-width: 992px) {
  .contenedor {
    width: 80%;
  }
}

a {
  text-decoration: none;
}

a:hover {
  color: initial;
}

h1 {
  font-size: 4.2rem;
  margin: 0 0 5rem 0;
}

h2,
h3 {
  margin: 0 0 3rem 0;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  border: none;
}

.seccion {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.seccion5 {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alto-min {
  min-height: 70vh;
}

.boton-fireBrick {
  background-color: #AC2C28;
  color: #fff;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3.75px;
  border: 1px solid #83211e;
  display: inline-block;
}
.boton-fireBrick:hover {
  background-color: #83211e;
  color: #fff;
}

.boton-fireBrick-block {
  background-color: #AC2C28;
  color: #fff;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3.75px;
  border: 1px solid #83211e;
  display: block;
}
.boton-fireBrick-block:hover {
  background-color: #83211e;
  color: #fff;
}

.boton-verde {
  background-color: #2D882D;
  color: #fff;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  border-radius: 3.75px;
  border: 1px solid #2D882D;
  display: inline-block;
}
.boton-verde:hover {
  background-color: #206220;
  color: #fff;
}

.boton-rojo {
  background-color: #D44E49;
  color: #fff;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  border-radius: 3.75px;
  border: 1px solid #D44E49;
  display: inline-block;
}
.boton-rojo:hover {
  background-color: #bd322d;
  color: #fff;
}

.header {
  background-color: #000;
  min-height: 30rem;
}
@media (min-width: 768px) {
  .header {
    min-height: 15rem;
    display: flex;
  }
}
.header a img {
  width: 100%;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .header a img {
    width: 75%;
  }
}
@media (min-width: 1500px) {
  .header a img {
    width: 50%;
  }
}
.header .contenido-header {
  padding: 2rem;
  max-width: none;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.imagen-header {
  background-image: url(../../build/img/bufalos_header2.jpg);
  background-position: center center;
  background-size: cover;
  position: relative;
  min-height: 20rem;
}
@media (min-width: 768px) {
  .imagen-header {
    min-height: 50rem;
  }
}

.contenido-imagen {
  font-family: "Suez One", serif;
  position: absolute;
  display: none;
  bottom: 0rem;
  left: 10rem;
  padding: 2rem;
}
@media (min-width: 1500px) {
  .contenido-imagen {
    bottom: 1rem;
  }
}
.contenido-imagen h2 {
  margin-bottom: 1rem;
  color: #EDEDED;
  letter-spacing: 0.5rem;
}
.contenido-imagen p {
  display: inline-block;
  color: #AC2C28;
  text-transform: uppercase;
  font-size: 6rem;
  letter-spacing: 2.5rem;
}
@media (min-width: 768px) {
  .contenido-imagen {
    display: block;
  }
}

.navegacion-principal {
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.3s ease-in-out, opacity, 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #EDEDED;
  font-size: 2rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #AC2C28;
}

.header-derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header-derecha {
    align-items: flex-end;
  }
}

.redes-sociales a {
  color: #EDEDED;
  font-size: 3rem;
}

.menu-mobile {
  color: #EDEDED;
  font-size: 4rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .menu-mobile {
    display: none;
  }
}

.mostrar {
  opacity: 1;
  visibility: visible;
}

.site-footer {
  background-color: #000;
  padding: 1rem;
  min-height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #fff;
  font-size: 1.2rem;
}

.no-webp .imagen-central {
  background-image: url(../../build/img/imagen_central.jpg);
}

.webp .imagen-central {
  background-image: url(../../build/img/imagen_central.webp);
}

.imagen-central {
  background-image: url(../../build/img/imagen_central.jpg);
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.imagen-central .overlay-central {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(30, 43, 22, 0.5);
  top: 0;
  left: 0;
}

.contenido-central {
  z-index: 10;
  text-align: center;
}
.contenido-central h3, .contenido-central p {
  color: #fff;
}

.contenido-destacado {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .contenido-destacado {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenido {
    margin-top: 0;
  }
}
.contenido h2 {
  margin-bottom: 2rem;
  color: #AC2C28;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1rem;
}
.contenido p {
  line-height: 1.8;
}

.logos-redes {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  justify-items: center;
  align-items: center;
}
.logos-redes img {
  width: 4rem;
  border-radius: 3.75px;
}
.logos-redes .youtube {
  width: 5rem;
}

.imagen {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.imagen .overlay-header {
  position: absolute;
  height: 100%;
  width: 100%;
}
.imagen img {
  width: 100%;
  display: block;
}

.contenedor-noticias {
  margin: 0 auto;
}
@media (min-width: 992px) {
  .contenedor-noticias {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1500px) {
  .contenedor-noticias {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.escala {
  transition: transform 0.3s ease;
}
.escala:hover {
  transform: scale(1.1);
}

.noticia {
  background-color: #fbf3e7;
  border-radius: 3.75px;
  margin-bottom: 2rem;
  transition: box-shadow 0.5s ease, transform 0.3s ease;
}
@media (min-width: 768px) {
  .noticia {
    margin-bottom: 0;
  }
}
.noticia:hover {
  box-shadow: 0 0 20px 2px #ac2c28;
}
.noticia img {
  border-top-left-radius: 3.75px;
  border-top-right-radius: 3.75px;
}
.noticia .contenido-noticias {
  padding: 2rem;
  border: 1px solid #EDEDED;
}
.noticia .contenido-noticias .texto-noticia {
  height: 20rem;
  overflow: hidden;
  mask-image: linear-gradient(to bottom, #fff 5%, transparent 90%);
}
.noticia .contenido-noticias h3 {
  margin-bottom: 2rem;
  color: #AC2C28;
}
.noticia .contenido-noticias h4 {
  font-size: 2rem;
}
.noticia .contenido-noticias p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.fecha {
  font-style: italic;
  color: #AC2C28;
}

.panel-admin {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

.barra-lateral {
  background-color: #000;
  color: #EDEDED;
}
.barra-lateral a {
  margin-top: 3rem;
  padding: 1rem 0;
  text-decoration: none;
  color: #EDEDED;
  font-size: 2rem;
  text-align: center;
}
.barra-lateral p {
  color: #EDEDED;
  text-align: center;
  padding: 1rem 0;
}

.listado-crud {
  margin-left: 5rem;
}
.listado-crud a {
  text-align: left;
  font-size: 2rem;
  margin-top: 0;
}
.listado-crud a:hover {
  color: #EDEDED;
  text-decoration: underline;
}

.sidebar-footer a {
  font-size: 2rem;
}
.sidebar-footer .ventana {
  padding: 1rem;
}

.central {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.alerta {
  margin: 1rem 0;
  padding: 1rem;
  color: #EDEDED;
  text-transform: uppercase;
  border-radius: 3.75px;
  font-weight: 600;
}
.alerta.error {
  background-color: #D44E49;
}
.alerta.exito {
  background-color: #2D882D;
}

.listado_noticias h1 {
  text-align: center;
}

#listado_noticias {
  margin-top: 2rem;
}
#listado_noticias td, #listado_noticias th {
  vertical-align: middle;
}

.acciones {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.acciones button, .acciones a {
  line-height: normal;
}

.contenedor-formulario {
  width: 50%;
  margin: 5rem auto;
}

.formulario {
  border: 1px solid #0C0C0C;
  border-radius: 3.75px;
  padding: 2rem;
}
.formulario label {
  font-size: 2rem;
  text-transform: uppercase;
  display: block;
  font-weight: 700;
  color: #0C0C0C;
}
.formulario input:not([type=submit], input[type=checkbox]),
.formulario textarea {
  padding: 1rem;
  border-radius: 3.75px;
  border: 1px solid gainsboro;
  width: 100%;
}
.formulario textarea {
  min-height: 10rem;
  height: auto;
}
.formulario .campo {
  margin-top: 1rem;
}

.contenedor-discos .disco {
  margin: 5rem 0;
  padding-bottom: 3rem;
  border-bottom: 1px solid gainsboro;
}
@media (min-width: 768px) {
  .contenedor-discos .disco {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    align-items: start;
  }
}
@media (min-width: 1500px) {
  .contenedor-discos .disco {
    align-items: center;
  }
}
.contenedor-discos .disco .contenido-disco {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .contenedor-discos .disco .contenido-disco {
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .contenido-historia {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.texto-historia {
  margin-top: 2rem;
}
.texto-historia h2 {
  color: #AC2C28;
}

.musico p {
  font-size: 1.6rem;
}

.noticia-interna {
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .contenedor-productos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .contenedor-productos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
