  border-top-right-radius: 3.75px;
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
}
.contenedor-productos .datos-producto {
  padding: 2rem;
  align-items: center;
}
.contenedor-productos .datos-producto h3 {
  margin-bottom: 1rem;
}
.contenedor-productos .datos-producto p {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .contenedor-productos .datos-producto p {
    margin: 0;
  }
}
.contenedor-productos .datos-producto a {
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.contenedor-productos .detalle-cantidad {
  align-items: center;
  padding: 0 0.75rem;
}
.contenedor-productos .detalle-cantidad label {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .contenedor-productos .detalle-cantidad label {
    text-align: right;
  }
}
.contenedor-productos .detalle-cantidad input {
  padding: 0.5rem 1rem;
}